@import "~bootstrap/dist/css/bootstrap.min.css";
@import "ColorPalette";
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');

$primary-font-color: #454250;
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  color: $primary-font-color;
}

.mainContainer {
  height: 100vh;
  //padding: 2rem;

  .profileCard {
    width: 100%;
    height: 24rem;
    border-radius: 1rem;
    background: #fff;
    border: 1px solid $shadeOne;
    position: relative;
    box-shadow: rgba(99, 99, 99, 0.2) 0 2px 8px 0;

    .coverPicture {
      height: 40%;
      width: 100%;
      background-image: url("./assets/images/coverImage.svg");
      background-color: $shadeOne;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      border-top-left-radius: 1rem;
      border-top-right-radius: 1rem;
    }

    .profilePicture {
      height: 10rem;
      width: 10rem;
      padding: 10px;
      position: absolute;
      top: 3rem;
      left: 2rem;
      background: #fff;
      border-radius: 0.5rem;

      .image {
        height: 100%;
        width: 100%;
        background-image: url("assets/images/profileImage.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 0.5rem;
      }
    }

    .profileDetails {
      padding-top: 3.5rem;
      padding-left: 1rem;

      .name {
        font-size: 24px;
        font-weight: 600;
        line-height: 28px;
      }

      .role {
        font-size: 16px;
        margin-bottom: 12px;
      }

      .info {
        display: flex;
        align-items: flex-start;
        margin-top: 8px;

        img {
          height: 20px;
          margin-right: 5px;
        }

        a {
          text-decoration: none;
          color: $primary-font-color;
        }
        a:hover{
          color: $shadeFour;
        }
      }
    }
  }
}