@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);

body{margin:0;font-family:"Open Sans",sans-serif;color:#454250}.mainContainer{height:100vh}.mainContainer .profileCard{width:100%;height:24rem;border-radius:1rem;background:#fff;border:1px solid #ececec;position:relative;box-shadow:rgba(99,99,99,.2) 0 2px 8px 0}.mainContainer .profileCard .coverPicture{height:40%;width:100%;background-image:url(/static/media/coverImage.43cc91b3.svg);background-color:#ececec;background-position:center;background-repeat:no-repeat;background-size:cover;border-top-left-radius:1rem;border-top-right-radius:1rem}.mainContainer .profileCard .profilePicture{height:10rem;width:10rem;padding:10px;position:absolute;top:3rem;left:2rem;background:#fff;border-radius:.5rem}.mainContainer .profileCard .profilePicture .image{height:100%;width:100%;background-image:url(/static/media/profileImage.a758efde.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;border-radius:.5rem}.mainContainer .profileCard .profileDetails{padding-top:3.5rem;padding-left:1rem}.mainContainer .profileCard .profileDetails .name{font-size:24px;font-weight:600;line-height:28px}.mainContainer .profileCard .profileDetails .role{font-size:16px;margin-bottom:12px}.mainContainer .profileCard .profileDetails .info{display:flex;align-items:flex-start;margin-top:8px}.mainContainer .profileCard .profileDetails .info img{height:20px;margin-right:5px}.mainContainer .profileCard .profileDetails .info a{text-decoration:none;color:#454250}.mainContainer .profileCard .profileDetails .info a:hover{color:#142d4c}
